import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import installation from '@/components/articles/installation' // 
import dependencies from '@/components/articles/dependencies' // 
import layout from '@/components/articles/layout' // 
import mainHeader from '@/components/articles/main-header' // 
import sidebar from '@/components/articles/sidebar' // 
import controlSidebar from '@/components/articles/control-sidebar' // 
import boxes from '@/components/articles/boxes' // 
import infoBox from '@/components/articles/info-box' // 
import directChat from '@/components/articles/direct-chat' // 
import jsLayout from '@/components/articles/js-layout' // js
import jsPushMenu from '@/components/articles/js-push-menu' // 
import jsTree from '@/components/articles/js-tree' // 
import jsControlSidebar from '@/components/articles/js-control-sidebar' // 
import jsBoxWidget from '@/components/articles/js-box-widget' // 
import jsBoxRefresh from '@/components/articles/js-box-refresh' // 
import jsTodoList from '@/components/articles/js-todo-list' // 
import browserSupport from '@/components/articles/browser-support' // 
import upgradeGuide from '@/components/articles/upgrade-guide' // 
import implementations from '@/components/articles/implementations' // 
import faq from '@/components/articles/faq' // FAQ
import license from '@/components/articles/license' // 

Vue.use(Router)

var router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/', // 
      name: 'installation',
      meta: {
        title: 'AdminLTE - '
      },
      component: installation
    },
    {
      path: '/installation', // 
      name: 'installation',
      meta: {
        title: 'AdminLTE - '
      },
      component: installation
    },
    {
      path: '/dependencies', // 
      name: 'dependencies',
      meta: {
        title: 'AdminLTE -  & '
      },
      component: dependencies
    },
    {
      path: '/layout', // 
      name: 'layout',
      meta: {
        title: 'AdminLTE - '
      },
      component: layout
    },
    {
      path: '/main-header', // 
      name: 'main-header',
      meta: {
        title: 'AdminLTE - '
      },
      component: mainHeader
    },
    {
      path: '/sidebar', // 
      name: 'sidebar',
      meta: {
        title: 'AdminLTE - '
      },
      component: sidebar
    },
    {
      path: '/control-sidebar', // 
      name: 'control-sidebar',
      meta: {
        title: 'AdminLTE - '
      },
      component: controlSidebar
    },
    {
      path: '/boxes', // 
      name: 'boxes',
      meta: {
        title: 'AdminLTE - '
      },
      component: boxes
    },
    {
      path: '/info-box', // 
      name: 'info-box',
      meta: {
        title: 'AdminLTE - '
      },
      component: infoBox
    },
    {
      path: '/direct-chat', // 
      name: 'direct-chat',
      meta: {
        title: 'AdminLTE - '
      },
      component: directChat
    },
    {
      path: '/js-layout', // Layout 
      name: 'js-layout',
      meta: {
        title: 'AdminLTE - Layout '
      },
      component: jsLayout
    },
    {
      path: '/js-push-menu', // 
      name: 'js-push-menu',
      meta: {
        title: 'AdminLTE - '
      },
      component: jsPushMenu
    },
    {
      path: '/js-tree', // 
      name: 'js-tree',
      meta: {
        title: 'AdminLTE - '
      },
      component: jsTree
    },
    {
      path: '/js-control-sidebar', // 
      name: 'js-control-sidebar',
      meta: {
        title: 'AdminLTE - '
      },
      component: jsControlSidebar
    },
    {
      path: '/js-box-widget', // 
      name: 'js-box-widget',
      meta: {
        title: 'AdminLTE - '
      },
      component: jsBoxWidget
    },
    {
      path: '/js-box-refresh', // 
      name: 'js-box-refresh',
      meta: {
        title: 'AdminLTE - '
      },
      component: jsBoxRefresh
    },
    {
      path: '/js-todo-list', // 
      name: 'js-todo-list',
      meta: {
        title: 'AdminLTE - '
      },
      component: jsTodoList
    },
    {
      path: '/browser-support', // 
      name: 'browser-support',
      meta: {
        title: 'AdminLTE - '
      },
      component: browserSupport
    },
    {
      path: '/upgrade-guide', // 
      name: 'upgrade-guide',
      meta: {
        title: 'AdminLTE - '
      },
      component: upgradeGuide
    },
    {
      path: '/implementations', // 
      name: 'implementations',
      meta: {
        title: 'AdminLTE - '
      },
      component: implementations
    },
    {
      path: '/faq', // 
      name: 'faq',
      meta: {
        title: 'AdminLTE - FAQ'
      },
      component: faq
    },
    {
      path: '/license', // 
      name: 'license',
      meta: {
        title: 'AdminLTE - '
      },
      component: license
    }
  ]
})
router.beforeEach((to, from, next) => {
  window.document.title = to.meta.title
  next()
})

router.afterEach((to, from, next) => {
  window.scrollTo(0, 0)
})

export default router



